import Bmob from "hydrogen-js-sdk";
import common from '@/utils/common.js';
import producers from '@/utils/producers.js';

let uid;
let that;
let fristClassId
let secondClassId
export default {
	data() {
		return {
			orderDet: {}
		}
	},
	onLoad(options) {
		that = this;
		uid = uni.getStorageSync("uid");
		uni.setNavigationBarTitle({
			title: "付款单详情"
		})

		const query = Bmob.Query('order_opreations');
		query.include("opreater", "account", "producer", "producer", "secondClass", "fristClass");
		query.get(options.id).then(res => {
			res.createdTime = res.createdTime.iso.split(" ")[0]
			that.orderDet = res;
		}).catch(err => {
			console.log(err)
		})
	},

	onShow() {

	},
	methods: {


	}
}
